










import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component({
  name: "CardLibButton",
  components: {}
})
export default class CardLibButton extends Vue {
  @Prop({ required: false, type: Boolean, default: false })
  isFullWidth?: boolean;

  @Prop({required: false, type: Boolean, default: false})
  disabled?: boolean;

  onClick() {
    this.$emit("button:click");
  }
}