




































































































































import { Component, Vue } from "vue-property-decorator";
    import CardLibButton from "@/components/CardLibButton.vue";
    import CardModal from "@/components/modals/CardModal.vue";
    import { IUserService } from "@/types/User";
    import { IUserGroupService } from "@/types/UserGroup";
    import { IUserRoleService } from "@/types/UserRole";

    @Component({
        name: "UserList",
        components: { CardLibButton, CardModal }
    })
    export default class UserList extends Vue {
        user: IUserService = {
            UserId:0,
            userName: "",
            password: "",
            createdOn: new Date(),
            updatedOn: new Date(),
            firstName: "",
            lastName: "",
            UserRoleId: 1,
            UserGroupId: 1
        };
        UserRole: IUserRoleService = {
            UserRoleId: 1,
            RoleName: "",
            createdOn: new Date(),
            updatedOn: new Date()
        };
        UserGroup: IUserGroupService = {
            UserGroupId: 1,
            GroupName: "",
            createdOn: new Date(),
            updatedOn: new Date()
        };

        save() {
            this.$emit("save:user", this.user);
        }

        close() {
            this.$emit("close");
        }
    }